<template>
  <div class="cms-list-layout">
    <cms-prompt collapse="true">
      <li>显示店铺订单</li>
      <li>点击订单号即可进入详情页面</li>
    </cms-prompt>
    <!-- 筛选区 -->
    <section class="cms-list-query">
      <div class="query-group">
        <label>订单号</label>
        <el-input @keyup.native.enter="query" v-model="queryParams.orderNum"></el-input>
      </div>
      <div class="query-group">
        <label>买家账号</label>
        <el-input @keyup.native.enter="query" v-model="queryParams.memberName"></el-input>
      </div>
      <div class="query-group">
        <label>订单状态</label>
        <el-select @change="query" v-model="queryParams.status">
          <el-option
            v-for="item in orderStatus"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <label>订单来源</label>
        <el-select @change="query" v-model="queryParams.source">
          <el-option label="全部" value=""></el-option>
          <el-option label="pc端" value="1"></el-option>
          <el-option label="手机端" value="2"></el-option>
          <el-option label="APP" value="3"></el-option>
          <el-option label="公众号 " value="4"></el-option>
          <el-option label="小程序" value="5"></el-option>
          <el-option label="平板" value="6"></el-option>
        </el-select>
      </div>
      <br>
      <div class="query-group">
        <label>订单类型</label>
        <el-select  @change="query" v-model="queryParams.orderType">
          <el-option label="全部"  value=""></el-option>
          <el-option label="实物订单" value="1"></el-option>
          <el-option label="虚拟订单" value="2"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <label>支付方式</label>
        <el-select v-model="queryParams.payMode" @change="query">
          <el-option label="全部" :value="''"></el-option>
          <el-option label="支付宝" :value="1"></el-option>
          <el-option label="微信" :value="2"></el-option>
          <el-option label="银联" :value="3"></el-option>
          <el-option label="余额" :value="4"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <label>下单时间</label>
        <cms-picker v-model="timer" @change="setTimer"></cms-picker>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker="true" @click="query">查询</cms-button>
      </div>
    </section>
    <el-tabs v-model="queryParams.status" @tab-click="changeOrderStatus">
      <el-tab-pane
        v-for="item in orderStatus"
        :key="item.value"
        :label="item.label"
        :name="item.value"
      ></el-tab-pane>
    </el-tabs>
    <!-- 数据列表 -->
    <table class="order-table" v-loading="loading">
      <tr>
        <th colspan="6">订单详情</th>
        <th>收货人</th>
        <th>实收款</th>
        <th>订单状态</th>
        <th>操作选项</th>
      </tr>
      <tbody v-for="(item,index) in tableData" :key="index">
        <div style="width:100%;height:25px;background:#fff;"></div>
        <!-- 头部 -->
        <tr class="order-header">
          <td colspan="11" style="border:1px solid #e0e0e0;">
            <span style="margin-left:20px">订单编号：</span>
            <span style="margin-right: 50px;">{{item.orderCode}}</span>
            <span>下单时间:</span>
            <span style="margin-right: 60px;">{{$moment(item.orderTime)}}</span>
          </td>
        </tr>
        <tr v-for="(product,index2) in item.items" :key="product.id">
          <td class="product-info" colspan="3">
            <!-- 商品信息 -->
            <div class="product__inner" v-if="item.items&&item.items.length>0">
              <img
                class="product-img"
                :src="$cms.filterUrl(product.productInfo.coverImageUrl)"
                v-if="product.productInfo"
              >
              <div class="product-title">
                <h3 class="title-h3" v-if="product.productInfo">{{product.productInfo.productName}}</h3>
                <p
                  class="title-p"
                  style="margin-top:10px;"
                  v-if="product.productInfo"
                >{{showAttr(product.productInfo.skuAttr)}}</p>
              </div>
            </div>
          </td>
          <td class="order-count">
            <!-- 价格 -->
            <span v-if="item.items&&item.items.length>0">￥{{$cms.float(product.productPrice)}}</span>
          </td>
          <td class="order-count">
            <!-- 数量 -->
            <span v-if="item.items&&item.items.length>0">x {{product.productCount}}</span>
          </td>
          <td class="order-count">
            <!-- 退换货状态 -->
            <div  v-if="item.newRefund">
              <span v-if="item.newRefund.lastIntervent">卖家拒绝<br>
              {{item.newRefund.lastIntervent.platformStatus===2?'（平台已拒绝）':''}}</span>
              <span v-else>{{item.newRefund.status|refund}}</span>
            </div>
          </td>
          <td :rowspan="item.items.length" v-if="index2==0" class="order-user">
            <span v-if="item.member">
              <i class="iconfont icon-yonghu" style="position: relative;top: 2px;"></i>
              {{item.member.username}}
            </span>
          </td>
          <td :rowspan="item.items.length" v-if="index2==0" class="tr-price">
            <p class="await">￥{{$cms.float(item.orderPayAmount)}}</p>
            <p style="color:#999999;margin: 10px 0 0 0">(含运费：￥{{$cms.float(item.orderFreight)}})</p>
            <p>
              <i class="iconfont" style="color:#00C700;margin-right:5px"></i>
              {{item.payMode |payMode}}
            </p>
          </td>
          <td :rowspan="item.items.length" v-if="index2==0" class="order-status">
            <div class="await cell">
              <span v-if="item.status==1" class="info">待付款</span>
              <span v-if="item.status==2" class="info">待发货</span>
              <span v-if="item.status==3" class="info">待收货</span>
              <span v-if="item.status==4" class="success">已完成</span>
              <span v-if="item.status==5" class="error">已取消</span>
              <span v-if="item.status==6" class="error">超时交易关闭</span>
            </div>
            <p
              style="color:#4196E4;margin: 10px 0 10px 0; cursor: pointer;"
              class="link"
              @click="$routerLink('/orderManger/view','view',{id:item.id})"
            >订单详情</p>
          </td>
          <td :rowspan="item.items.length" v-if="index2==0" class="order-operation">
            <div v-if="item.status==1" class="mb-10">
              <cms-button
                size="small"
                type="info"
                slot="reference"
                style="width:94px"
                @click="setChangPrice(item)"
              >修改价格</cms-button>
            </div>
            <div class="mb-10" v-if="item.status<3">
              <cms-button size="small" type="info" slot="reference" style="width:94px"
               @click="printOrder(item.id)">打印订单</cms-button>
            </div>
             <div class="mb-10" v-if="item.status===3">
              <cms-button size="small" type="info" slot="reference" style="width:94px"
               @click="printwayBillOrder(item)">打印运单</cms-button>
            </div>
            <div class="mb-10" v-if="item.status==1">
              <cms-button
                size="small"
                type="info"
                slot="reference"
                style="width:94px"
                @click="cancelOrder(item.id)"
              >取&nbsp;&nbsp;消</cms-button>
            </div>
        
          </td>
        </tr>
      </tbody>
    </table>
    <cms-pagination @change="getPage" :total="total" :size="5"></cms-pagination>
     <change-price ref="changePrice" @change="updateList"></change-price>
    <print-page ref="print" style="display:none"></print-page>
  </div>
</template>
<script>
import list from '@/mixins/list'
import {
  orderPage,
  cancelOrder
} from '@/api/shop/order.js'
import {
  waybillTemplateList
} from '@/api/setting/waybillTemplate.js'
import changePrice from './changePrice.vue'
import printPage from './shipBill.vue'

var defaultData = {
  page: 1,
  size: 10,
  source: '',
  orderType: '',
  status: '',
  payMode: '',
  orderNum: '',
  orderTimeBegin: '',
  orderTimeEnd: '',
  memberName: ''
}
export default {
  name: 'orderList',
  mixins: [list],
  components: {
    changePrice,
    printPage

  },
  data() {
    return {
      timer: [],
      activeStatus: '',
      orderStatus: [
        {
          label: '全部',
          value: '0'
        },
        {
          label: '待付款',
          value: '1'
        },
        {
          label: '待发货',
          value: '2'
        },
        {
          label: '待收货',
          value: '3'
        },
        {
          label: '已完成',
          value: '4'
        },
        {
          label: '已取消 ',
          value: '5'
        }
      ],
      queryParams: Object.assign({}, defaultData)

    }
  },
  filters: {
    payMode(value) {
      if (value === '') {
        return ''
      } else {
        const payMode = ['', '支付宝', '微信', '银联', '余额']
        return payMode[value]
      }
    },
    refund(value) {
      const statusMode = [
        '待卖家确认',
        '退款中',
        '退款成功',
        '退款失败',
        '待买家发货',
        '待卖家收货',
        '待买家收货',
        '待卖家发货',
        '已完成',
        '买家已取消',
        '平台介入处理中'
      ]
      return statusMode[value]
    }
  },
  methods: {
    updateList(status) {
      if (status) {
        this.query()
      }
    },
    setChangPrice(item) {
      this.$refs.changePrice.setChangPrice(item)
    },
    cancelOrder(orderId) {
      this.$confirm('是否确认取消该订单', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(res => {
          cancelOrder({ orderId: orderId }).then(res => {
            if (res.code === 200) {
              this.$successMessage('取消成功')
              this.query()
            } else {
              this.$errorMessage(res.message)
            }
          })
        })
        .catch(() => {})
    },
    printwayBillOrder(data) { // 打印运单
      let printUrl = ''
      const info = data.orderExt
      const wayBillInfo = {
        orederId: info.expressNum,
        senderName: info.sellerName,
        senderTel: info.sellerTel,
        senderCompany: '',
        senderAddress: info.sellerAddress,
        senderCode: info.postalCode,
        desc: '',
        receiverName: info.customerName,
        receiverTel: info.customerTel,
        receiverAddress: info.customerAddress,
        receiverCode: info.customerAreaCode
      }
      waybillTemplateList({ waybillType: 2 }).then(res => {
        if (res.code === 200) {
          localStorage.setItem('wayBillList', JSON.stringify(res.data))
          localStorage.setItem('wayBillInfo', JSON.stringify(wayBillInfo))
          if (process.env.NODE_ENV === 'production') {
            const path = location.pathname.substring(0, location.pathname.lastIndexOf('/'))
            printUrl = path + '/static/print/wayBillPrint.html'
          } else {
            printUrl = '/static/print/wayBillPrint.html'
          }
          window.open(printUrl)
        }
      })
    },
    printOrder(id) {
      this.$refs.print.getOrderInfo(id).then(res => {
        var printStr = res.innerHTML
        let printUrl = ''
        if (process.env.NODE_ENV === 'production') {
          const path = location.pathname.substring(0, location.pathname.lastIndexOf('/'))
          printUrl = path + '/static/print/print.html'
        } else {
          printUrl = '/static/print/print.html'
        }
        var page = window.open(printUrl, '')
        setTimeout(() => {
          page.document.body.innerHTML = printStr
          return false
        }, 50)
      })
    },
    changeOrderStatus() {
      this.query()
    },
    setTimer(timer) {
      // 设置时间区间
      this.queryParams.orderTimeBegin = timer[0]
      this.queryParams.orderTimeEnd = timer[1]
      this.query()
    },
    showAttr(data) { // 转换json字符串属性
      if (data === '') {
        return ''
      } else {
        const arr = JSON.parse(data)
        let str = ''
        arr.forEach(v => {
          const tmp = v.name + ':' + v.value + '  '
          str = str + tmp
        })
        return str
      }
    }
  },
  created() {
    this.getTableData(orderPage, this.queryParams)
  }
}
</script>

<style scoped lang='scss'>

</style>




